const ADD_POST = "ADD-POST";
const UPD_POST = "UPDATE-NEW_POST_TEXT";

let initialState = {
  posts: [
    { id: 1, message: "Я сегодня была в кино" },
    { id: 2, message: "Завтра пойду в парк" },
  ],
  newPost: "",
}

const postsReducer = (state = initialState, action) => {
  switch(action.type){
    case ADD_POST:
      let newPost = {
        id: 6,
        message: state.newPost,
      };
      state.posts.push(newPost);
      state.newPost = "";
      return state;
    case UPD_POST:
      state.newPost = action.newText;
      return state;
    default:
      return state;
  }
};

export const addPostActionCreator = () => ({
  type: ADD_POST,
});

export const updateNewPostTextActionCreator = (newText) => ({
  type: UPD_POST,
  newText: newText,
});

export default postsReducer;
