import React from "react";
import ReactDom from "react-dom";
import style from "./AddPost.module.css";
import {
  addPostActionCreator,
  updateNewPostTextActionCreator,
} from "../../../redux/posts-Reducer";

class AddPost extends React.Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.addPost = this.addPost.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  addPost() {
    this.props.dispatch(addPostActionCreator());
  }

  onChange() {
    let text = this.myRef.current.value;
    this.props.dispatch(updateNewPostTextActionCreator(text));
  }

  render() {
    return (
      <div className={style.addpost}>
        <textarea
          onChange={this.onChange}
          ref={this.myRef}
          value={this.props.newPost}
        />
        <button onClick={this.addPost}>Добавить пост</button>
      </div>
    );
  }
}

export default AddPost;
