const ADD_MESSAGE = "ADD-MESSADGE";
const UPD_BODY = "UPDATE-NEW_MESSADGE_BODY";

let initialState = {
  messages: [{ content: "Привет" }, { content: "Как дела" }],
  chats: [
    { name: "Даша Тарасова" },
    { name: "AliExpress" },
    { name: "Citilink" },
  ],
  newMessage: "",
};

const messagesReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGE:
      let newMessadge = {
        content: state.newMessage,
      };
      state.messages.push(newMessadge);
      state.newMessage = "";
      return state;
    case UPD_BODY:
      state.newMessage = action.newBody;
      return state;
    default:
      return state;
  }
};

export const addMessadgeActionCreator = () => ({
  type: ADD_MESSAGE,
});

export const updateNewMessadgeTextActionCreator = (newBody) => ({
  type: UPD_BODY,
  newBody: newBody,
});

export default messagesReducer;
