import React from "react";
import style from "./Dialogs.module.css";
import Chat from "./Chat/Chat";
import Messages from "./Messages/Messages";

import {
  addMessadgeActionCreator,
  updateNewMessadgeTextActionCreator,
} from "../../redux/messages-Reducer";

class Dialogs extends React.Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.sendMail = this.sendMail.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  sendMail() {
    this.props.dispatch(addMessadgeActionCreator());
  }

  onChange() {
    let text = this.myRef.current.value;
    this.props.dispatch(updateNewMessadgeTextActionCreator(text));
  }

  render() {
    let chats = this.props.messagesPage.chats.map((item, index) => {
      return <Chat key={index} name={item.name} />;
    });

    let messages = this.props.messagesPage.messages.map((item, index) => {
      return <Messages key={index} content={item.content} />;
    });

    return (
      <div className={style.colomn}>
        <div className={style.chats}>{chats}</div>
        <div className={style.messages}>
          {messages}
          <textarea
            onChange={this.onChange}
            ref={this.myRef}
            value={this.props.messagesPage.newMessage}
          ></textarea>
          <button onClick={this.sendMail}>Отправить</button>
        </div>
      </div>
    );
  }
}

export default Dialogs;
