import React from "react";
import ReactDom from "react-dom";
import Post from "./Post/Post";

class MyPosts extends React.Component {
  constructor(props){
    super(props)
  }
  render() {
    let postElement = this.props.postsPageData.map((item,index) => {
      return <Post key={index} data={item.message} />;
    });

    return <>{postElement}</>;
  }
}

export default MyPosts;
